import { createContext, useContext, useMemo, useState } from 'react';

const AuthenticationContext = createContext();

function useAuthObj() {
 const context = useContext(AuthenticationContext)
 if (!context) {
  throw new Error(`useAuthObj must be used within a AuthenticationProvider`)
 }

 return context;
}

function AuthenticationProvider(props) {
 const [userObj, setUserObj] = useState({
  userName: '',
  userFullName: 'Guest',
  schoolName: '',
  schoolLogo: ''
 });

 // const value = useMemo(() => [userObj, setUserObj], [userObj])
 return <AuthenticationContext.Provider value={userObj, setUserObj} {...props} />
}

export { AuthenticationProvider, useAuthObj }

